.xiOn-space {
  display: inline-flex;

  &-vertical {
    flex-direction: column;
  }

  &-align {
    &-center {
      align-items: center;
    }

    &-start {
      align-items: flex-start;
    }

    &-end {
      align-items: flex-end;
    }

    &-baseline {
      align-items: baseline;
    }
  }
}

.xiOn-space-item {
  &:empty {
    display: none;
  }
}

.xiOn-space {
  &-rtl {
    direction: rtl;
  }
}

